// FONTS
@mixin fonty($size: 22px, $weight: 400, $lineH: 30px) {
  font-size: $size;
  font-weight: $weight;
  line-height: $lineH;
}

// BLOCKS
@mixin centry($width) {
  margin: 0 auto;
  width: $width;
}

@mixin flexy($direction: row, $justify: center, $align: center) {
  display: flex;
  flex-direction: $direction;
  justify-content: $justify;
  align-items: $align;
}

@function rem($pixels, $min: 16) {
  @if (unitless($pixels)) {
    $pixels: $pixels * 1px;
  }
  @if (unitless($min)) {
    $min: $min * 1px;
  }
  $context: 16px;

  @return #{'max('}#{$pixels / $context * 1rem}#{', '}#{$min}#{')'};
}
