@import '../../scss/mixins';
@import '../../scss/vars';

.docs {
  background-color: rgba(41, 51, 63, 1);
  padding: 4em 0em;
  color: $mainFontColor;

  .container {
    @include centry(90%);
    font-size: rem(12px);
  }

  details {
    margin: 2em 0em;
  }
  summary {
    cursor: pointer;
    font-size: 1.5rem;
    text-transform: uppercase;
  }

  table {
    border-collapse: collapse;
    border: 1px solid white;
  }

  td {
    padding: 0.35em 0.4em;
    border: 1px solid white;
  }

  .codeExample {
    width: unset;
  }
}
