@import 'mixins';

body {
  margin: 0;
  padding: 1% 5%;
  color: $mainFontColor;
  background: linear-gradient(
    90deg,
    rgba(90, 95, 98, 1) 0%,
    rgba(117, 122, 125, 1) 28%,
    rgba(148, 153, 156, 1) 65%,
    rgba(176, 180, 183, 1) 100%
  );
}

p {
  margin-block: 0 0;
  margin-inline: 0 0;
}

.pl1 {
  padding-left: 1em !important;
}
.pl2 {
  padding-left: 2em !important;
}
.pl3 {
  padding-left: 3em !important;
}

.width10 {
  width: 10%;
}

.width45 {
  width: 45%;
}

.codeExample {
  @include flexy(column);
  width: 60%;
  color: rgba(245, 245, 5, 1);
  letter-spacing: 2px;

  .alignLeft {
    font-size: 1.25rem;
    align-self: flex-start;
    padding: 0.1em 0em 0.25em 0.5em;
    // padding-top: 0.1em;
    // padding-bottom: 0.25em;

    &:not(:first-child):not(:last-child) {
      padding-left: 2em;
    }
  }
}

.btnExample {
  padding: 0.5em;
  border: 1px solid white;
  border-radius: 15px;
  box-shadow: 0px 8px 15px 0px rgba(31, 81, 255, 0.5);
}

.TB1padd {
  padding: 1em 0em;
}

.hide {
  display: none;
}
