@import '../../scss/mixins';
@import '../../scss/vars';

.first {
  @include flexy();
  gap: 5%;
  padding: 5%;
  background-color: rgba(214, 214, 214, 1);
  color: $titleFontColor;

  &__left {
    @include flexy(column, space-evenly, flex-start);
    width: 45%;
    height: 400px;

    .h1 {
      text-transform: uppercase;
      font-size: 3.3em;
      font-weight: 800;
      margin-block: 0 0;

      &__middle {
        font-size: 1.5em;
      }
    }

    p {
      font-size: 1.4em;
      font-style: oblique;
    }
  }

  &__files {
    @include flexy();
    width: 100%;
    a {
      @include flexy();
      gap: 20px;
      box-sizing: border-box;
      text-decoration: none;
      text-transform: capitalize;
      font-size: 1.4em;
      text-align: center;

      //display: block;
      color: white;
      background-color: $titleFontColor;
      transition: all 0.6s ease;
      padding: 1em 2em;
      margin: 1em;

      &:hover {
        background-color: white;
        color: $titleFontColor;
      }

      img {
        width: 30px;
        height: 30px;
      }
    }
  }

  &__right {
    @include flexy();
    width: 45%;
    height: 500px;
  }
}
