@import '../../scss/mixins';

.example {
  width: 90%;
  margin: 1em auto;

  font-size: rem(12px);
  color: rgba(243, 238, 238, 1);
  border: 1px solid rgba(252, 252, 252, 1);

  div {
    border: 1px solid rgba(252, 252, 252, 1);
  }

  &__container {
    @include flexy(row, center, normal);
  }

  &__title {
    font-size: rem(18px);
    font-weight: 700;
    padding: 1em;
  }

  &__content {
    padding: 1em;
    width: 40%;

    p {
      padding: 0.2em 0em;
    }
  }
}
