@import '../../scss/mixins';
@import '../../scss/vars';

.header {
  @include flexy(row, space-evenly);
  width: 100%;
  height: 60px;
  background: rgb(232, 232, 232, 1);
  position: relative;

  .logo {
    width: 60px;
    height: 20px;
    background-color: rgba(54, 166, 222, 1);
  }

  ul {
    margin: 0 0;
    padding: 0 0;

    li {
      list-style-type: none;
    }
  }

  a {
    font-size: 1rem;
    text-transform: uppercase;
    font-weight: 500;
    color: $titleFontColor;
    box-sizing: border-box;
    text-decoration: none;
    position: relative;
  }

  .nav {
    width: 60%;

    &__link_active {
      &::before,
      &::after {
        content: '';
        width: 20px;
        height: 12px;
        position: absolute;
      }
      &:before {
        left: -15%;
        top: -15%;
        border-left: 3px inset $titleFontColor;
        border-top: 3px inset $titleFontColor;
      }

      &:after {
        right: -15%;
        bottom: -15%;
        border-right: 3px inset $titleFontColor;
        border-bottom: 3px inset $titleFontColor;
      }
    }

    &__list {
      width: 100%;
      @include flexy(row, space-between);
    }
  }
}
